


































*{
    margin:0;
    padding:0
}
li{
    list-style:none
}
.list li{
    height:90px;
    line-height:90px;
    background:#f6f6f6;
    margin-top:2px;
    cursor:pointer
}
.list span{
    display:inline-block;
    vertical-align:middle;
    padding-left:50px
}
.list img{
    width: 80px;
    height: 80px;
    background:#666;
    border-radius:5px;
    display:inline-block;
    vertical-align:middle;
    margin-left:5px
}























































#head{
  height:50px;
  background:#a00303;
  text-align:center;
  line-height:50px;
  color:white;
  font-weight:bolder;
  position:fixed;
  width:100%
}
.headBox{
  height:50px
}
.back{
  font-size:30px;
  position:absolute;
  left:30px;
  top:-5px;
  -webkit-transform:scale(1,1.5);
          transform:scale(1,1.5);
  cursor:pointer
}
.toTop{
    width:50px;
    height:50px;
    background:#a00303;
    color:white;
    position:fixed;
    bottom:20px;
    right:20px;
    font-size:12px;
    text-align:center;
    line-height:25px;
    cursor:pointer
}
